<template>
	<view class="order_btn" :class="{'bg_black':bgStyle=='bg_black','large_btn':isLarge}" >
		<slot>按钮文字</slot>
	</view>
</template>

<script>
	export default {
		data(){
			return{
			}
		},
		props:{
			// 背景颜色 深色背景
			bgStyle:{
				default:'bg_gray',
				type:String
			},
			// 按钮是否是大按钮
			isLarge:{
				default:false,
				type:Boolean
			}
		}
	}
</script>

<style class="scss">
	.order_btn {
		width:235upx;
		height:80upx;
		background:rgba(246,246,246,1);
		border:1px solid rgba(238,238,238,1);
		border-radius:40upx;
		text-align: center;
		line-height: 80upx;
		color: #333333;
		font-weight: 500;
		cursor: pointer;
	}
	.large_btn {
		width:680upx;
		}
	.order_btn.bg_black {
		background: #000000;
		color: white;
	}
</style>
